/**************************************************************************/
/***************************    Create Pup    *****************************/
/**************************************************************************/

input {
	border: none !important;
	outline: none !important;
}

input:focus {
	border: none !important;
	outline: none !important;
}

.input_box {
    background-color: #060b13 !important;
    border: none !important;
    outline: none !important;
    font-size: 0.95em;
    color: rgb(175, 175, 175) !important;
    box-shadow:  inset 5px 5px 6px #000205, inset -3px -3px 7px #101925 !important;
}

.input_box:active {
    background-color: #060b13 !important;
    border: none !important;
    outline: none !important;
}

.checkbox_container {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-top: 30px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
  
/* Hide the browser's default checkbox */
.checkbox_container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkbox_container > span > p {
    margin-left: 35px !important;
    font-size: 0.7em;
}
  
/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    border-radius: 3px;
    background-color: #060b13;
    box-shadow:  inset 5px 5px 6px #000205, inset -3px -3px 7px #101925 !important;
}
  
/* On mouse-over, add a purple background color */
.checkbox_container:hover input ~ .checkmark {
    background-color: #523ba8;
    box-shadow: none !important;
}
  
/* When the checkbox is checked, add a purple background */
.checkbox_container input:checked ~ .checkmark {
    background-color: #523ba8;
    box-shadow: none !important;
}
  
/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}
  
/* Show the checkmark when checked */
.checkbox_container input:checked ~ .checkmark:after {
    display: block;
}
  
/* Style the checkmark/indicator */
.checkbox_container .checkmark:after {
    left: 8px;
    top: 4px;
    width: 5px;
    height: 10px;
    border: solid rgb(226, 225, 225);
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}



