/* Hamburger */
.hamburger {
	position: absolute;
	width: 60px;
	height: 60px;
	z-index: 2;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
}

.hamburger > div {
	position: absolute;
	width: 60%;
	height: 3px;
	border-radius: 1.2px;
	background-color: #0d0d0d;
	transition: 0.4s;
}

.hamburger > div:before {
	content: "";
	position: absolute;
	width: 75%;
	height: 3px;
	border-radius: 1.5px;
	background-color: #0d0d0d;
	top: -10px;
	right: 0;
	transition: 0.4s;
}

.hamburger > div:after {
	content: "";
	position: absolute;
	width: 85%;
	height: 3px;
	border-radius: 1.5px;
	background-color: #0d0d0d;
	top: 10px;
	right: 0;
	transition: 0.4s;
}

.toggler:hover + .hamburger > div:before {
	width: 100%;
}

.toggler:hover + .hamburger > div:after {
	width: 100%;
}

.toggler:checked + .hamburger > div:before {
	width: 100%;
}

.toggler:checked + .hamburger > div:after {
	width: 100%;
}

.toggler:checked + .hamburger > div {
	-webkit-transform: rotate(135deg);
	        transform: rotate(135deg);
	background-color: #fff;
}

.toggler:checked:hover + .hamburger > div {
	-webkit-transform: rotate(225deg);
	        transform: rotate(225deg);
	background-color: #fff;
}

.toggler:checked + .hamburger > div:before,
.toggler:checked + .hamburger > div:after {
	top: 0;
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
	background-color: #fff;
}

/*
 * Make this absolute positioned
 * at the top left of the screen
 */
#menu {
	position: absolute;
	width: 100vw;
	margin: -100px 0 0 0;
	padding: 50px;
	padding-top: 125px;
	right: -120px;
	background: #0d0d0d;
	list-style-type: none;
	-webkit-font-smoothing: antialiased;
	/* to stop flickering of text in safari */
	-webkit-transform-origin: 0% 0%;
	        transform-origin: 0% 0%;
	-webkit-transform: translate(100%, 0);
	        transform: translate(100%, 0);
	transition: -webkit-transform 1.5s cubic-bezier(0.77, 0.2, 0.05, 1);
	transition: transform 1.5s cubic-bezier(0.77, 0.2, 0.05, 1);
	transition: transform 1.5s cubic-bezier(0.77, 0.2, 0.05, 1), -webkit-transform 1.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

#menu li {
	padding: 10px 0;
	font-size: 22px;
}

/*
 * fade it in from the left
 */
#menuToggle input:checked ~ div {
	-webkit-transform: scale(1, 1);
	        transform: scale(1, 1);
	opacity: 1;
}

.hamburger_container {
	height: 100vh;
	-ms-scroll-chaining: none;
	    overscroll-behavior: contain;
	overflow-x: hidden !important;
}

.menu_box {
	text-align: center;
	width: 100%;
	max-width: 1500px;
	left: 50%;
	top: 45%;
	position: relative;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	font-size: 16px;
	max-height: 100%;
}

.toggler:checked + .hamburger + #menu > .hamburger_container > .menu_box {
	-webkit-animation: menu_content_fade_in 1.5s ease-in-out;
	        animation: menu_content_fade_in 1.5s ease-in-out;
}

.toggler:not(:checked) + .hamburger + #menu {
	transition: -webkit-transform 2s cubic-bezier(0.77, 0.2, 0.05, 1) 0.5s;
	transition: transform 2s cubic-bezier(0.77, 0.2, 0.05, 1) 0.5s;
	transition: transform 2s cubic-bezier(0.77, 0.2, 0.05, 1) 0.5s, -webkit-transform 2s cubic-bezier(0.77, 0.2, 0.05, 1) 0.5s;
}

.toggler:not(:checked) + .hamburger + #menu > .hamburger_container > .menu_box {
	-webkit-animation: menu_content_fade_out 0.4s ease-out;
	        animation: menu_content_fade_out 0.4s ease-out;
	opacity: 0;
}

@-webkit-keyframes menu_content_fade_in {
	0% {
		opacity: 0;
	}
	75% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes menu_content_fade_in {
	0% {
		opacity: 0;
	}
	75% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@-webkit-keyframes menu_content_fade_out {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@keyframes menu_content_fade_out {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@-webkit-keyframes slide_up {
	0% {
		-webkit-transform: translateY(100px);
		        transform: translateY(100px);
		opacity: 0;
	}

	90% {
		-webkit-transform: translateY(100px);
		        transform: translateY(100px);
		opacity: 0;
	}

	100% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
		opacity: 1;
	}
}

@keyframes slide_up {
	0% {
		-webkit-transform: translateY(100px);
		        transform: translateY(100px);
		opacity: 0;
	}

	90% {
		-webkit-transform: translateY(100px);
		        transform: translateY(100px);
